import React, { useEffect, useState } from 'react'
import { getMeets, createMeets } from '../api/meet'

export default function MeetsPage() {

  const [data, setData] = useState({})
  const [meets, setMeets] = useState([])

  useEffect(() => {
    getMeets().then(res => setMeets(res))
    console.log(meets)
  })

  const hadleFormSubmit = event => {
    event.preventDefault()
    createMeets(data).then(res => console.log(res))
    getMeets().then(res => setMeets(res))
  }

  const hadleInputChange = event => setData({
    ...data, [event.target.name]: event.target.value
  })

  return <>
    <form onSubmit={hadleFormSubmit}>
      <input name="name" type="text" placeholder="nombre" onChange={hadleInputChange} />
      <input name="date" type="date" placeholder="fecha" onChange={hadleInputChange} />
      <input name="hour" type="time" placeholder="hora" onChange={hadleInputChange} />
      <textarea name="description" placeholder="descripcion" onChange={hadleInputChange}></textarea>
      <input type="submit" value="crear meet" />
    </form>
    {/* {meets.map(meet=><div key={'meet-' + meet.uuid}>
      {meet.name} {meet.date}
      <Link to={'/meet/' + meet.uuid}>ir</Link>
      {meet.me ? <Link to={'/meet/' + meet.uuid + '/edit'}>editar</Link>:<></>}
    </div>)} */}
  </>
}