const api = process.env.REACT_APP_BASE_URL

export function signUp(data) {
  return fetch(`${api}acount/register/`,{
    method: 'POST', body: JSON.stringify(data),
    headers: { 'Content-Type': 'application/json' },
  }).then(res => res.json())
}

export function signIn(data) {
  return fetch(`${api}auth`,{
    method: 'POST', body: JSON.stringify(data),
    headers: {'Content-Type': 'application/json'},
  }).then(res => res.json())
}

export function getCategories() {
  return fetch(`${api}acount/categories/`,{
    method: 'GET',
  }).then(res => res.json())
}

export function getSocialRed() {
  return fetch(`${api}acount/SocialRed/`, {
    method: 'GET'
  }).then(res => res.json())
}

export function me() {
  return fetch(`${api}acount/me/`, {
    method: 'GET',
    headers: {
      'Content-Type': 'application/json',
      'Authorization': `Token ${sessionStorage.getItem('token')}`
    },
  }).then(res => res.json())
}

export function picture(body) {
  return fetch(`${api}acount/picture/`, {
    method: 'POST', body: body
  }).then(res => res.json())
}