import React, { useEffect, useState} from 'react'
import { useParams } from 'react-router-dom'

import { createCard, editCard, deleteCard, getCards } from '../api/meet'

function QuestionEditForm(props) {

  const [isVisible, setVisible] = useState(true)
  const [question, setQuestion] = useState(props.question)

  const hadleFormSubmit = event => {
    event.preventDefault()
    editCard(question).then(res => console.log(res))
  }

  const deleteQuestion = () => deleteCard(question.id).then(() => {
    setVisible(!isVisible)
  })

  const hadleInputChange = event => setQuestion({
    ...question, [event.target.name]: event.target.value
  })

  return <form onSubmit={hadleFormSubmit} style={{display: isVisible ? 'block' : 'none'}}>
    <input type="text" name="title" placeholder="titulo" defaultValue={question.title} onChange={hadleInputChange} />
    <input type="text" name="time" placeholder="hh:mm:ss" parent="[0-9]{2}:[0-9]{2}:[0-9]{2}" defaultValue={question.time} onChange={hadleInputChange}/>
    <textarea type="text" name="description" placeholder="pregunta" defaultValue={question.description} onChange={hadleInputChange} />
    <input type="submit" value="Guardar" />
    <button onClick={deleteQuestion}>Borrar</button>
  </form>
}

export default function QuestionsPage() {
  
  const { uuid } = useParams()
  const [questions, setQuestions] = useState([])
  const [question, setQuestion] = useState({})

  const hadleFormSubmit = event => {
    event.preventDefault()
    createCard({
      ...question, type: 'question', meet: uuid, order: questions.length
    }).then(() => {
      getCards(uuid).then(res => setQuestions(res))
    })
    event.target.reset()
  }

  const hadleInputChange = event => setQuestion({
    ...question, [event.target.name]: event.target.value
  })

  useEffect(() => {
    getCards(uuid).then(res => setQuestions(res))
  })

  return <>
    <h1>Preguntas</h1>
    {questions.map((question, key) => <QuestionEditForm key={key} question={question}/>)}
    <form onSubmit={hadleFormSubmit}>
      <input type="text" name="title" placeholder="titulo" onChange={hadleInputChange} />
      <input type="text" name="time" parent="[0-9]{2}:[0-9]{2}:[0-9]{2}" placeholder="hh:mm:ss" onChange={hadleInputChange} />
      <textarea type="text" name="description" placeholder="pregunta" onChange={hadleInputChange} />
      <input type="submit" value="Guardar" />
    </form>
  </>
}