import React, { useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import {
    Container, Button, Paper, Grid,
} from '@material-ui/core'

import './meet.scss'
import { me } from '../api/acount'
import { role } from '../api/meet'
import { room as roomWS  } from '../api/room'

import Chat from '../components/chat'
import Mgs from '../components/mgs'
import FrameJitsi from '../components/frame-jitsi'

export default function MeetPages(){
    
    const { uuid } = useParams()
    const [user, setUser] = useState({})
    const [roleMeet, setRoleMeet] = useState('')
    const [meetEvent, setMeetEvent] = useState({frame: 'welcome'})
    const [ws, setws] = useState()
    const [timer, setTimer] = useState({minutes: 10, seconds: 0})
    const [guests, setGuests] = useState([])
    const [room, setRoom] = useState({name: undefined, url:undefined})

    useEffect(() => {
        me().then(res => setUser(res))
        role(uuid).then(res => setRoleMeet(res.role))
        setTimer({ minutes: 10, seconds: 0})
    }, [])

    useEffect(() =>{
        if (ws !== undefined) {
            ws.onmessage = event => {
                const data_json = JSON.parse(event.data)
                if (data_json.type === 'guests') setGuests(data_json.data)
                else if (data_json.type === 'go_networking') {
                    setMeetEvent({
                    'frame': 'networking', data: data_json.data
                    })
                    for (let x in data_json.data) if (data_json.data[x].couples.indexOf(user.username) >= 0) {
                        setRoom(data_json.data[x].room)
                    } else console.log(data_json.data[x].couples.indexOf(user.username))
                    // Timer(10, 0)
                }
            }
        } else setws(roomWS(uuid))
    }, [ws, user])

    const Timer = (minutes, seconds) => {
        const totalSeconds = (minutes * 60) + seconds - 1
        if (totalSeconds >= 0) {
            const timeMinutes = parseInt(totalSeconds / 60)
            const timeSeconds = totalSeconds - (timeMinutes * 60)
            setTimeout(() => {
                setTimer({ minutes: timeMinutes, seconds: timeSeconds })
                Timer(timeMinutes, timeSeconds)
            }, 1000)
        }
    }

    const goNetworking = () => {
        ws.send(JSON.stringify({type: 'go_networking'}))
    }

    const Center = (props) => {
        const { event, meRole, room } = props
        if (meRole === 'mc') {
            return <FrameJitsi video={true} audio={true} room={room} displayCapture={true} />
        } else if (meRole === 'guest'){
            if (event.frame === 'welcome') return <Mgs />
            else if (event.frame === 'networking') {
                return <FrameJitsi video={true} audio={true} displayCapture={true} room={room} />
            }
        } else return <h1 style={{background: '#000'}}>No valido</h1>
    }

    const User = () => <Grid container className="user-name">
        <Grid item xs={8}>{user.full_name}</Grid>
        <Grid item xs={4}>
            <div className="user-photo">
                <div className="user-img" />
            </div>
        </Grid>
    </Grid>

    return <Container style={{ maxWidth: 'calc(100% - 20px)'}}>
        <Grid container justify="space-between" className="bar-meet">
            <Grid item xs={3} md={2} lg={2}>
                <Grid container>
                    <Grid item xs={4}>
                        <div className="bar-meet-container">
                            <div className="bar-meet-ico" />
                        </div>
                    </Grid>
                    <Grid item xs={8}>Live</Grid>
                </Grid>
            </Grid>
            <Grid item xs={3} md={2} lg={2}>
                Tiempo: {timer.minutes < 10 ? '0' + timer.minutes : timer.minutes}:{timer.seconds < 10 ? '0' + timer.seconds : timer.seconds}
            </Grid>
            <Grid item xs={3} md={1} lg={2}>
                <User />
            </Grid>
        </Grid>
        <Grid container justify="center" alignItems="flex-end" style={{margin: '10px 0px', color: '#fff'}}>
            <Grid item xs={12} md={4}>
                <div className="logo-user">
                    tu logo aqui
                </div>
            </Grid>
            <Grid item xs={12} md={4} style={{textAlign: 'right'}}>
                Bienvenidos
            </Grid>
        </Grid>
        <Grid container spacing={3}>
            <Grid item xs={12} md={3} lg={2}>
                <Paper className="meet-seccion">
                    Invitados: {guests.length}
                    <Grid container spacing={1}>
                        {guests.map((quest, key) => <Grid item xs={12} key={key}>
                            {quest.first_name} {quest.last_name}
                        </Grid>)}
                    </Grid>
                </Paper>
                {roleMeet === 'mc' ? <Button variant="contained" onClick={goNetworking}>
                    inicar networking
                </Button> : null}
            </Grid>
            <Grid item xs={12} md={6} lg={8}>
                <Paper className="meet-center">
                    <Center event={meetEvent} meRole={roleMeet} room={room.url}/>
                </Paper>
            </Grid>
            <Grid item xs={12} md={3} lg={2}>
                <Paper className="meet-seccion">
                    <Chat room={room} meet={uuid} />
                </Paper>
            </Grid>
        </Grid>
    </Container>
}
