import React from 'react'
import { BrowserRouter, Route, Switch } from "react-router-dom";

import './App.scss'
import AppNavigation from './components/app-navigation';

import HomePage from './pages/home'
import MeetPage from './pages/meet'
import EditMeetPage from './pages/edit-meet'
import MeetsPage from './pages/meets'
import LoginPage from './pages/login'
import QuestionsPage from './pages/questions'

export default function App(){
  return <BrowserRouter>
    <div className="app-frame" style={{
      background: 'linear-gradient(90deg, #fd997a 0%, #901473 100%)',
      position: 'fixed',
      height: '100%',
      width: '100%',
      zIndex: '-1000'
    }}></div>
    <AppNavigation />
    <div style={{
      paddingTop: '30px',
    }}>
      <Switch>
        <Route exact path='/' component={HomePage} />
        <Route exact path='/meet/:uuid' component={MeetPage} />
        <Route exact path='/meet/:uuid/edit' component={EditMeetPage} />
        <Route exact path='/meet/:uuid/questions' component={QuestionsPage} />
        <Route exact path='/meets' component={MeetsPage} />
        <Route exact path='/login/:uuid' component={LoginPage} />
      </Switch>
    </div>
  </BrowserRouter>
}
