import React, { useState, useEffect } from 'react'
import { Button, Grid, Tab, Tabs, TextField } from '@material-ui/core'
import { useParams } from 'react-router-dom'

import { chat } from '../api/room'

export default function Chat(props) {

    const { uuid } = useParams()
    const [generalChat, setGeneralChat] = useState([])
    const [roomChat, setRoomChat] = useState([])
    const [room, setRoom] = useState()
    const [value, setValue] = useState(0)
    const [data, setData] = useState({})
    
    const [ws, setws] = useState()

    const hadleTabs = (e, val) => setValue(val)

    useEffect(() => {
        setRoom(props)
    }, [props])

    useEffect(() => {
        if (ws !== undefined) {
            ws.onmessage = event => {
                const data_json = JSON.parse(event.data)
                console.log(data_json)
                if (data_json.chat === uuid) {
                    setGeneralChat(generalChat.concat(data_json.data))
                } else {
                    setRoomChat(roomChat.concat(data_json.data))
                }
            }
        } else setws(chat(uuid))
    }, [ws, generalChat])

    const hadleFormSubmit = event => {
        event.preventDefault()
        ws.send(JSON.stringify({
            chat: value == 0 ? room.meet : room.room.name_room,
            data: {
                msg: value == 0 ? data[uuid] : data['room']
            },
        }))
    }

    const hadleInputChange = event => setData({
        ...data, [event.target.name]: event.target.value
    })

    return <>
        <Tabs value={value} onChange={hadleTabs}>
            <Tab label="Chat General" />
            <Tab label="Chat de mesa" />
        </Tabs>
        <TabPanel value={value} index={0}>
            <Grid item xs={12}>
                <Grid item xs={12}>
                    {generalChat.map((msg, key) => <div style={{ display: 'block' }} key={key}>
                        {msg.msg}
                    </div>)}
                </Grid>
                <form onSubmit={hadleFormSubmit}>
                    <TextField label="username" variant="outlined" name={uuid} onChange={hadleInputChange} />
                    <Button variant="contained"onClick={hadleFormSubmit}>
                        Enviar
                </Button>
                </form>
            </Grid>
        </TabPanel>
        <TabPanel value={value} index={1}>
            <Grid item xs={12}>
                <Grid item xs={12}>
                    {roomChat.map((msg, key) => <div style={{display: 'block'}} key={key}>
                        {msg.msg}
                    </div>)}
                </Grid>
                <form onSubmit={hadleFormSubmit}>
                    <TextField label="username" variant="outlined" name="room" onChange={hadleInputChange} />
                    <Button variant="contained" onClick={hadleFormSubmit}>
                        Enviar
                    </Button>
                </form>
            </Grid>
        </TabPanel>
    </>
}

const TabPanel = (props) => {

    const { children, value, index } = props
    
    return <div style={{ display: value === index ? 'block' : 'none' }}>
        { children }
    </div>
}