import React, { useState, useEffect } from 'react'

import { Button, FormControl, Grid, InputLabel, TextField, Select } from '@material-ui/core'

import { signUp, getCategories, getSocialRed } from '../api/acount'

export default function SignUp(props) {

  const [data, setData] = useState({})
  const [categories, setCategories] = useState([])
  const [socialRed, setSocialRed] = useState([])
  const [picture, setPicture] = useState()
  const [socialRedProfile, setSocialRedProfile] = useState([])
  const { meet } = props
  
  useEffect(() => {
    getCategories().then(res => setCategories(res))
    getSocialRed().then(res => setSocialRed(res))
  }, [])

  const hadleInputChange = event => setData({
    ...data, [event.target.name]: event.target.value
  })

  const hadleInputChangeSocialRed = event => setSocialRedProfile({
    ...socialRedProfile, [event.target.name]: event.target.value
  })

  const hadleSelectCategoryChange = event => setData({
    ...data, 'category': event.target.value
  })

  const handleInputChangePicture = event => setPicture({
    selectedFile: event.target.files[0],
    loaded: 0,
  })

  const hadleFormSubmit = (event) => {
    event.preventDefault()
    signUp({ ...data, meet, social_red: socialRedProfile }).then(() => {})
  }

  return <form onSubmit={hadleFormSubmit}>
    <Grid container spacing={2}>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Username" variant="outlined" name="username" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Nombres" variant="outlined" name="first_name" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Apellidos" variant="outlined" name="last_name" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Compa침ia" variant="outlined" name="company" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Correo electr칩nico" variant="outlined" name="email" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <FormControl variant="outlined">
          <InputLabel htmlFor="age-native-required">Selecionar industria</InputLabel>
          <Select native onChange={hadleSelectCategoryChange}>
            <option aria-label="None" value="" />
            {categories.map(value => <option key={'category-' + value.id} value={value.id}>
              {value.name}
            </option>)}
          </Select>
        </FormControl>
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Cargo" name="role" variant="outlined" onChange={hadleInputChange} />
      </Grid>
      {socialRed.map(value => <Grid item xs={12} md={6} key={'social-red-' + value.id}>
        <TextField variant="outlined" label={value.name} name={value.name} onChange={hadleInputChangeSocialRed} />
      </Grid>)}
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="pagina web" name="web" variant="outlined" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12} md={6} lg={6}>
        <TextField label="Contrase침a" name="password" variant="outlined" type="password" onChange={hadleInputChange} />
      </Grid>
      <Grid item xs={12}>
        <input type="file" accept="image/png, image/jpeg" name="picture" onChange={handleInputChangePicture} />
      </Grid>
      <Grid item xs={12} className="text-align-center ">
        <Button variant="contained" onClick={hadleFormSubmit}>
          Registrarse
        </Button>
      </Grid>
    </Grid>
  </form>
}
