import React, { useState } from 'react'

import { useHistory } from 'react-router-dom'
import { Container, Button, Grid, TextField } from '@material-ui/core'

import { signIn } from '../api/acount.js'

export default function SignIn(props) {

  const  history = useHistory()
  const { meet } = props
  const [data, setData] = useState({})

  const hadleInputChange = event => setData({
    ...data, [event.target.name]: event.target.value
  })

  const hadleFormSubmit = (event) => {
    event.preventDefault()
    signIn({ ...data, role: 'viewer' }).then(res => {
      console.log(res)
      if(Object.keys(res).indexOf('token') > -1) {
        sessionStorage.setItem('token', res.token)
        history.push(`/meet/${meet}`)
      } else {
        console.log('error')
      }
    })
  }

  return <form onSubmit={hadleFormSubmit}>
    <Container maxWidth='xs'>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <TextField label="username" variant="outlined" name="username" onChange={hadleInputChange} />
        </Grid>
        <Grid item xs={12}>
          <TextField label="Contrase침a" type="password" variant="outlined" name="password" onChange={hadleInputChange} />
        </Grid>
        <Grid item xs={12} className="text-align-center ">
          <Button variant="contained" onClick={hadleFormSubmit}>
            Login
          </Button>
        </Grid>
      </Grid>
    </Container>
  </form>
}