import React, { useEffect, useState } from 'react'
import { Link } from 'react-router-dom'
import { useParams } from 'react-router-dom'

import { getMeets, updateMeet } from '../api/meet'

export default function  EditMeetPage() {

  const { uuid } = useParams()
  const [meet, setMeet] = useState({})

  useEffect(() => {
    getMeets(uuid).then(res => setMeet(res[0]))
  })

  const hadleFormSubmit = event => {
    event.preventDefault()
    updateMeet(uuid, meet).then(res => console.log(res))
  }

  const hadleInputChange = event => setMeet({
    ...meet, [event.target.name]: event.target.value
  })

  return <>
    <form onSubmit={hadleFormSubmit}>
      <input type="text" name="name" defaultValue={meet.name} onChange={hadleInputChange} />
      <textarea name="description" defaultValue={meet.description} onChange={hadleInputChange} />
      <input name="date" type="date" placeholder="fecha" onChange={hadleInputChange} />
      <input name="hour" type="time" placeholder="hora" onChange={hadleInputChange} />
      <input type="submit" value="guardar" />
    </form>
    <Link to={'/meet/' + uuid + '/questions'}>
      Preguntas
    </Link>
  </>
}