import React from 'react'

export default function FrameJitsi(props) {
    const { video, audio, displayCapture, room } = props
    let permise = ['fullscreen']
    if (video) permise.push('camera')
    if (audio) permise.push('microphone')
    if (displayCapture) permise.push('display-capture')
    console.log(room)
    return <iframe title="Frame" allow={permise.join('; ')} src={room == undefined ? 'https://prueba.eventograma.com/prueba' : room} style={{
        height: '100%', width: '100%', border: '0px'
    }} />
}