import React, { useState } from 'react'
import { useParams } from 'react-router-dom'

import { Container, Grid, Paper, Tab, Tabs } from '@material-ui/core'

import BackgroundImages from '../components/background-images'
import SignIn from '../components/signin'
import SignUp from '../components/signup'

export default function LoginPage() {

  const { uuid } = useParams()

  const [value, setValue] = useState(0)
  const hadleTabs = (e, val) => setValue(val)

  return <>
    <BackgroundImages />
    <Container maxWidth="md">
      <Paper square>
        <Grid container>
          <Grid item xs={12} style={{textAlign: 'center'}}>
            <div variant="contained" className="event-logo">
              Tu Logo
            </div>
          </Grid>
        </Grid>
        <Tabs value={value} onChange={hadleTabs}>
          <Tab className="login-tabs" label="SIGN UP" />
          <Tab className="login-tabs" label="SIGN IN" />
        </Tabs>
        <TabPanel value={value} index={0}>
          <SignUp meet={uuid} />
        </TabPanel>
        <TabPanel value={value} index={1}>
          <SignIn meet={uuid} />
        </TabPanel>
      </Paper>
    </Container>
  </>
}

function TabPanel(props) {
  const { children, value, index } = props
  return <div className="tab-panel">
    {value === index && <div>{children}</div>}
  </div>
}